import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomePage.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },

  { path: '/login', component: () => import('../views/LoginIn') },
  {
    path: '/home',
    name: 'home',
    component: HomeView,
    children: [

      { path: '/firstPage', name: 'first', component: () => import('../views/FirstPage') },
      { path: '/DescPage', name: 'desc', component: () => import('../views/DescPage') },
      { path: '/stuManage', name: 'stu', component: () => import('../views/StuManage') },
      { path: '/teaManage', name: 'tea', component: () => import('../views/TeacherManage') },
      {
        path: '/lessonDetail',
        name: 'lessonDetail',
        component: () => import('../views/LessonDetail.vue')
      },
      {
        path: '/articalManage',
        name: 'artical',
        component: () => import('../views/ArticalManage.vue')
      },
      {
        path: '/dataManage',
        name: 'dataManage',
        component: () => import('../views/DataManage.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
