// 根据url获取query参数
const getQuery = (url, name) => {
  const index = url.indexOf('?')
  if (index !== -1) {
    const queryStrArr = url.substr(index + 1).split('&')
    for (let i = 0; i < queryStrArr.length; i++) {
      const itemArr = queryStrArr[i].split('=')
      if (itemArr[0] === name) {
        return itemArr[1]
      }
    }
  }
  return null
}

/**
 *对学生数据操作
 */

// 定义获取全部学生信息
Mock.mock('/api/getAll/stuInfo', 'get', () => {
  return {
    status: 200,
    message: '获取数据成功',
    list: stuList,
    total: stuList.length
  }
})
// 定义分页获取学生信息
Mock.mock(/\/api\/getByPage\/stuInfo/, 'get', (options) => {
  // 获取传递的参数 pageIndex
  const pageIndex = getQuery(options.url, 'pageIndex')
  // 获取传递的参数 pageSize
  const pageSize = getQuery(options.url, 'pageSize')
  // 截取数据的起始位置
  const start = (pageIndex - 1) * pageSize
  // 截取数据的终点位置
  const end = pageIndex * pageSize
  // 计算总页数
  const toatlPage = Math.ceil(stuList.length / pageSize)
  // 数据的起始位置:(pageIndex-1)*pageSize  数据的结束位置:pageIndex*pageSize
  const list = pageIndex > toatlPage ? [] : stuList.slice(start, end)
  return {
    status: 200,
    message: '获取学生数据成功',
    list: list,
    total: stuList.length
  }
})
// 定义添加学生的数据
Mock.mock('/api/add/stuInfo', 'post', (options) => {
  const body = JSON.parse(options.body)
  stuList.push(Mock.mock({
    Id: stuList.length + 1,
    stu_Id: '@id',
    stu_Name: body.stu_Name,
    title: body.title,
    stu_Address: body.stu_Address,
    date: '@date(yyyy-MM-dd)'
  }))
  return {
    status: 200,
    message: '添加学生数据成功',
    list: stuList,
    total: stuList.length
  }
})
// 定义修改学生数据
Mock.mock('/api/edit/stuInfo', 'post', (options) => {
  const body = JSON.parse(options.body)
  const index = stuList.findIndex(item => item.Id === body.Id)
  stuList.splice(index, 1, body)
  return {
    status: 200,
    message: '修改成功',
    list: stuList,
    total: stuList.length
  }
})
// 定义删除学生的接口
Mock.mock('/api/del/stuInfo', 'post', (options) => {
  const body = JSON.parse(options.body)
  const index = stuList.findIndex(item => item.stu_Id === body.id)
  stuList.splice(index, 1)
  return {
    status: 200,
    message: '删除学生成功',
    list: stuList,
    total: stuList.length
  }
})

